
  /******************************************/

  body {
    padding: 0;
    margin: 0;
    background-color: #130d0a;
    font-family: "Playfair Display", serif;
    color: #fff;
    
  }
  
  #hero {
    height: 800px;
    overflow: hidden;
    position: relative;
  }
  
  #content {
    background-color: #130d0a;
  }
  
  .layer {
    background-position: bottom center;
    background-size: auto;
    background-repeat: no-repeat;
    width: 100%;
    height: 800px;
    position: fixed;
    z-index: -1;
  }
  
  #hero-mobile {
    display: none;
    background: url("../../../Assets/backgroundHobbiesHeader2.png") no-repeat center bottom/cover;
    height: 320px;
  }
  
  .first-section {
    padding: 3rem;
    margin: 2rem;
  }
  
  .text-header {
    font-size: 50px;
    text-align: center;
  }
  
  h1 {
    line-height: 120%;
    margin-bottom: 30px;
  }
  
  p {
    color: #ede0d5;
    font-size: 18px;
    line-height: 150%;
  }
  
  #hero,
  .layer {
    min-height: 800px;
    max-width: 1900px;
    margin: 0 auto;
    overflow: hidden;
  }
  
  .layer-bg {
    background-image: url("../../../Assets//unsplash_assets//b5.jpg");
     background-size: cover;
    background-position: center;
    background-size: contain;
}
  
 
  
  .layer-1 {
    background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/272781/ilu_02.png");
  }
  
  .layer-2 {
    background-image: url("../../../Assets//infinites/o3.png");
    background-size: 40%;
    padding-top: 5rem;
    background-position: right
  }
  
  .layer-3 {
    background-image: url("../../../Assets//infinites/Lazron.png");
    background-size: 40%;
    padding-top: 6rem !important;
    background-position: left;
  }
  
  .layer-4 {
    background-image: url("../../../Assets//infinites/unisonmasked.png");
    background-size: 30%;
    padding-right: 3rem;
    
  }
  .layer-5 {
    background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/272781/ilu_02.png");
}
  

  .layer-6 {
    background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/272781/ilu_01.png");
  }


  .layer-overlay {
    background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/272781/ilu_overlay.png");
  }
  
  @media only screen and (max-width: 768px) {
    #hero {
      display: none;
    }
  
    #hero-mobile {
      display: block;
    }
  }
  .tutorial-link {
    color: #fff;
    font-size: 18px;
    text-decoration: underline;
  }
  .tutorial-link:hover {
    color: #ede0d5;
  }


  